// reset & initial settings

*, 
*::after, 
*::before{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    border: 0;
    font-family: 'Roboto', sans-serif;
}

a{
    text-decoration: none;
}

ul,
ol,
li{
    list-style: none;
}

img{
    vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6{
    font-weight: inherit;
    font-size: inherit;
}

html,
body{
    height: 100%;
    line-height: 1;
    font-family: 'Roboto', sans-serif;
}

img{
    user-select: none;
}

/* [class*="__container"]{
    
} */

.wrapper{
    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0,0.6);
    overflow: hidden;
}

.btn__default{
    outline: none;
    border: none;
    border-radius: 5px;
    background-color: rgb(211, 64, 133);
    overflow: hidden;
    cursor: pointer;
    user-select: none;
    position: relative;
    color: white;
    font-size: 16px;

    &:after{
        content: "";
        display: block;
        width: 20px;
        height: 200px;
        margin-left: 50px;
        background: #fff;
        background: linear-gradient(90deg,hsla(0,0%,100%,.1) 10%,hsla(0,0%,100%,.2) 20%,hsla(0,0%,100%,.6));
        left: -40px;
        top: -100px;
        z-index: 1;
        transform: rotate(45deg);
        position: absolute;
        animation: movingFlare 2.5s ease-in-out 0.05s infinite;
    }
}

@keyframes movingFlare {
    0% {
        left: -30px;
        margin-left: 0px;
    }
    30% {
        left: 110%;
        margin-left: 80px;
    }
    100% {
        left: 110%;
        margin-left: 80px;
    }
}