.last__step{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 650px;
    flex-direction: column;
    padding: 0 30px;
    
    p.title{
        font-size: 36px;
        font-weight: 700;
        line-height: 140%;
        color: #363636;
        text-align: center;
    }

    p.text{
        font-weight: 400;
        margin-top: 15px;
        color: rgba(54, 54, 54, 0.7);
        font-size: 18px;
        text-align: center;
    }
}

@media (max-width: 1280px){
    .last__step{
        height: 100vh;
    }
}

@media (max-width: 990px){
    .last__step{
        
        p.title{
            font-size: 24px;
            line-height: 100%;
        }
    
        p.text{
            font-size: 16px;
        }
    }
    
}