.first__step{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 100%;
}

.first__step__content{
    width: 500px;
    padding: 0 30px;
    p.header{
        font-size: 26px;
        font-weight: 700;
        color: #363636;
        position: relative;
        line-height: 140%;

        &::after{
            content: '';
            position: absolute;
            width: 40px;
            height: 3px;
            background-color: #363636;
            top: -20px;
            left: 0;
        }
    }

    p.text{
        font-weight: 400;
        margin-top: 15px;
        color: rgba(54, 54, 54, 0.7);
        font-size: 16px;
    }

    .btn__default{
        width: 155px;
        height: 55px;
        font-weight: 500;
        margin-top: 30px;
    }
}

.first__step__images{
    width: 700px;
    height: 650px;

    picture{

        source, img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

@media (max-width: 1280px){
    .first__step__images{
        width: 55%;
        height: 100vh;
    }

    .first__step__content{
        width: 45%;
    }
    
}

@media (max-width: 990px){
    .first__step__images{
        width: 100%;
        height: 400px;
        order: 1;
        overflow: hidden;

        picture{

            source, img{
                width: 100%;
                height: auto;
                object-fit: 100%;
            }
        }
    }

    .first__step__content{
        width: auto;
        margin-top: 60px;
        order: 2;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0 30px;

        p.header{
            width: 500px;
            text-align: center;
    
            &::after{
                left: 50%;
                transform: translateX(-50%);
            }
        }
    
        p.text{
            text-align: center;
        }
    }

    .first__step{
        justify-content: flex-start;
        flex-direction: column;
        width: 100%;
        height: 100vh;
        overflow: auto;
        padding-bottom: 50px;
    }
    
    
}

@media (max-width: 768px){
    .first__step__content{
        width: 100%;

        p.header{
            width: 100%;
        }

        .btn__default{
            width: 100%;
        }
    }
}

@media (max-width: 604px){
    .first__step__images{
        height: auto;
    }
}