.quiz{
    width: 1200px;
    height: 650px;
    background-color: rgb(243, 243, 243);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.steps__wrapp{
    width: 1200px;
    height: 650px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0 30px;
}

.steps__content{
    height: calc(100% - 104px - 61px);
    display: flex;
    padding: 20px 0;
    flex-direction: column;
    overflow: auto;
}

.steps__footer{
    width: 100%;
    height: 104px;
    display: flex;
    justify-content: flex-end;
    align-items: center;

    position: relative;

    &::after{
        content: '';
        position: absolute;
        width: calc(100% + 60px);
        height: 1px;
        background-color: #d9d9d9;
        top: 0;
        left: -30px;
    }

    .btn__prev{
        width: 43px;
        height: 43px;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 0;
        outline: 0;
        border: 1px solid #dbdbdb;
        border-radius: 3px;
        cursor: pointer;
        background-color: transparent;
        margin-right: 15px;

        svg{
            width: 18px;
            height: 18px;
            fill: rgba(0,0,0,.5);
        }
    }

    .btn__next{
        display: flex;
        align-items: center;
        height: 43px;
        padding: 0 20px;

        svg{
            fill: white;
            margin-left: 5px;
            width: 18px;
            height: 18px;
        }
    }
}

.steps__header{
    width: 100%;
    height: 61px;
    display: flex;
    align-items: center;
    position: relative;

    &::after{
        content: '';
        position: absolute;
        width: calc(100% + 60px);
        height: 1px;
        background-color: #d9d9d9;
        bottom: 0;
        left: -30px;
    }

    p.title{
        font-weight: 400;
        color: rgba(54, 54, 54, 0.7);
        font-size: 14px;
        margin-left: 10px;
    }

    svg{
        width: 20px;
        height: 20px;
        fill: #d34085;
    }
}

@media (max-width: 1280px){
    .quiz{
        width: 100%;
        height: 100vh;
        top: 0;
        left: 0;
        transform: translate(0, 0);
        position: fixed;
    }

    .steps__wrapp{
        width: 100%;
        height: 100vh;
    }
}

#step0, #step1, #step2, #step3, #step4, #step5, #step6, #step7, #step8, #step9, #step10, #other_steps, #last_step {display: none;}

#step0{
    display: block;
}