/* stylelint-disable */
*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
  font-family: "Roboto", sans-serif;
}

a {
  text-decoration: none;
}

ul,
ol,
li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

html,
body {
  height: 100%;
  line-height: 1;
  font-family: "Roboto", sans-serif;
}

img {
  user-select: none;
}

/* [class*="__container"]{

} */
.wrapper {
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
  overflow: hidden;
}

.btn__default {
  outline: none;
  border: none;
  border-radius: 5px;
  background-color: rgb(211, 64, 133);
  overflow: hidden;
  cursor: pointer;
  user-select: none;
  position: relative;
  color: white;
  font-size: 16px;
}
.btn__default:after {
  content: "";
  display: block;
  width: 20px;
  height: 200px;
  margin-left: 50px;
  background: #fff;
  background: linear-gradient(90deg, hsla(0deg, 0%, 100%, 0.1) 10%, hsla(0deg, 0%, 100%, 0.2) 20%, hsla(0deg, 0%, 100%, 0.6));
  left: -40px;
  top: -100px;
  z-index: 1;
  transform: rotate(45deg);
  position: absolute;
  animation: movingFlare 2.5s ease-in-out 0.05s infinite;
}

@keyframes movingFlare {
  0% {
    left: -30px;
    margin-left: 0px;
  }
  30% {
    left: 110%;
    margin-left: 80px;
  }
  100% {
    left: 110%;
    margin-left: 80px;
  }
}
.quiz {
  width: 1200px;
  height: 650px;
  background-color: rgb(243, 243, 243);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.steps__wrapp {
  width: 1200px;
  height: 650px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0 30px;
}

.steps__content {
  height: calc(100% - 104px - 61px);
  display: flex;
  padding: 20px 0;
  flex-direction: column;
  overflow: auto;
}

.steps__footer {
  width: 100%;
  height: 104px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: relative;
}
.steps__footer::after {
  content: "";
  position: absolute;
  width: calc(100% + 60px);
  height: 1px;
  background-color: #d9d9d9;
  top: 0;
  left: -30px;
}
.steps__footer .btn__prev {
  width: 43px;
  height: 43px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0;
  outline: 0;
  border: 1px solid #dbdbdb;
  border-radius: 3px;
  cursor: pointer;
  background-color: transparent;
  margin-right: 15px;
}
.steps__footer .btn__prev svg {
  width: 18px;
  height: 18px;
  fill: rgba(0, 0, 0, 0.5);
}
.steps__footer .btn__next {
  display: flex;
  align-items: center;
  height: 43px;
  padding: 0 20px;
}
.steps__footer .btn__next svg {
  fill: white;
  margin-left: 5px;
  width: 18px;
  height: 18px;
}

.steps__header {
  width: 100%;
  height: 61px;
  display: flex;
  align-items: center;
  position: relative;
}
.steps__header::after {
  content: "";
  position: absolute;
  width: calc(100% + 60px);
  height: 1px;
  background-color: #d9d9d9;
  bottom: 0;
  left: -30px;
}
.steps__header p.title {
  font-weight: 400;
  color: rgba(54, 54, 54, 0.7);
  font-size: 14px;
  margin-left: 10px;
}
.steps__header svg {
  width: 20px;
  height: 20px;
  fill: #d34085;
}

@media (max-width: 1280px) {
  .quiz {
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    transform: translate(0, 0);
    position: fixed;
  }
  .steps__wrapp {
    width: 100%;
    height: 100vh;
  }
}
#step0, #step1, #step2, #step3, #step4, #step5, #step6, #step7, #step8, #step9, #step10, #other_steps, #last_step {
  display: none;
}

#step0 {
  display: block;
}

.first__step {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}

.first__step__content {
  width: 500px;
  padding: 0 30px;
}
.first__step__content p.header {
  font-size: 26px;
  font-weight: 700;
  color: #363636;
  position: relative;
  line-height: 140%;
}
.first__step__content p.header::after {
  content: "";
  position: absolute;
  width: 40px;
  height: 3px;
  background-color: #363636;
  top: -20px;
  left: 0;
}
.first__step__content p.text {
  font-weight: 400;
  margin-top: 15px;
  color: rgba(54, 54, 54, 0.7);
  font-size: 16px;
}
.first__step__content .btn__default {
  width: 155px;
  height: 55px;
  font-weight: 500;
  margin-top: 30px;
}

.first__step__images {
  width: 700px;
  height: 650px;
}
.first__step__images picture source, .first__step__images picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (max-width: 1280px) {
  .first__step__images {
    width: 55%;
    height: 100vh;
  }
  .first__step__content {
    width: 45%;
  }
}
@media (max-width: 990px) {
  .first__step__images {
    width: 100%;
    height: 400px;
    order: 1;
    overflow: hidden;
  }
  .first__step__images picture source, .first__step__images picture img {
    width: 100%;
    height: auto;
    object-fit: 100%;
  }
  .first__step__content {
    width: auto;
    margin-top: 60px;
    order: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 30px;
  }
  .first__step__content p.header {
    width: 500px;
    text-align: center;
  }
  .first__step__content p.header::after {
    left: 50%;
    transform: translateX(-50%);
  }
  .first__step__content p.text {
    text-align: center;
  }
  .first__step {
    justify-content: flex-start;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    overflow: auto;
    padding-bottom: 50px;
  }
}
@media (max-width: 768px) {
  .first__step__content {
    width: 100%;
  }
  .first__step__content p.header {
    width: 100%;
  }
  .first__step__content .btn__default {
    width: 100%;
  }
}
@media (max-width: 604px) {
  .first__step__images {
    height: auto;
  }
}
.last__step {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 650px;
  flex-direction: column;
  padding: 0 30px;
}
.last__step p.title {
  font-size: 36px;
  font-weight: 700;
  line-height: 140%;
  color: #363636;
  text-align: center;
}
.last__step p.text {
  font-weight: 400;
  margin-top: 15px;
  color: rgba(54, 54, 54, 0.7);
  font-size: 18px;
  text-align: center;
}

@media (max-width: 1280px) {
  .last__step {
    height: 100vh;
  }
}
@media (max-width: 990px) {
  .last__step p.title {
    font-size: 24px;
    line-height: 100%;
  }
  .last__step p.text {
    font-size: 16px;
  }
}